footer{
    background: var(--color-primary); padding: 3rem 0; text-align: center; font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg);
}
.footer__logo{
    font-size: 2rem; font-weight: 600; margin-bottom: 2rem; display: inline-block;
}
.permalinks{
    display: flex;flex-wrap: wrap; justify-content: center; gap: 2rem; margin: 0 auto 3rem;
}
.footer__socials{
    display: flex; justify-content: center; gap: 1rem; margin-bottom: 4rem;
}
.footer__socials a{
    background: var(--color-white);
     /* color: var(--color-white); */
     padding: 0.5rem; border-radius: .7rem; display: flex; border: 1px solid transparent; 
     transition: var(--transition); font-size: 1.5rem;
}
.footer__socials a:hover{
    color: var(--color-primary); 
}
.footer__copyright{
    margin-bottom: 4rem; color: var(--color-bg);
}

@media only screen and (max-width:600px) {
    .permalinks{
        flex-direction: column; gap: 1.2rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}

@media only screen and (max-width:340px) {
    footer{
        width: 100%;
    }
    .permalinks{
        flex-direction: column; gap: 1.2rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}